let user = {}, otherUser = {}, userType = 1, token = '';

export function getToken()
{

	return token;
}

export function setToken(newToken)
{	
	token = newToken;
}

export function getAuthUser()
{
	return fetch('https://api.wteam.chat/user-service/auth/user', {
			headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*' }
		}).then(res => res.json())
                          .then(data => {
                            return Promise.resolve(data);
                          })
                          .catch(err => {
                            console.log('members error: ', err);
     						return Promise.reject(err);
                          });
}

export function getUser(id)
{
	return fetch('https://api.wteam.chat/user-service/user/' + id + '/info', {
			headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*' }
		}).then(res => res.json())
                          .then(data => {
                            return Promise.resolve(data);
                          })
                          .catch(err => {
                            console.log('members error: ', err);
     						return Promise.reject(err);
                          });
}

export function setUserType(type)
{
	userType = type;
}

export function getUserType()
{
	return userType;
}

export function setUserInfo(userInfo)
{
	user = userInfo;
}

export function getUserInfo()
{
	return user;
}

export function setOtherUserInfo(userInfo)
{
	otherUser = userInfo;
}

export function getOtherUserInfo()
{
	return otherUser;
}

export function getMessages(id)
{
	return fetch('https://api.wteam.chat/messaging-service/messages/' + id, {
		  headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*', 'Content-Type': 'application/json' },
		  method: 'GET'
		}).then(res => res.json())
                          .then(data => {
                            return Promise.resolve(data);
                          })
                          .catch(err => {
                            console.log('members error: ', err);
     						return Promise.reject(err);
                          });
}

export function getRoomMessages(id)
{
  return fetch('https://api.wteam.chat/messaging-service/messages/' + id + '/room', {
      headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*', 'Content-Type': 'application/json' },
      method: 'GET'
    }).then(res => res.json())
                          .then(data => {
                            return Promise.resolve(data);
                          })
                          .catch(err => {
                            console.log('members error: ', err);
                 return Promise.reject(err);
                          });
}

export function sendMessage(message)
{
	return fetch('https://api.wteam.chat/messaging-service/message', {
		  headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*', 'Content-Type': 'application/json' },
		  method: 'POST',
		  body: JSON.stringify(message)
		}).then(res => res.json())
                          .then(data => {
                            return Promise.resolve(data);
                          })
                          .catch(err => {
                            console.log('members error: ', err);
     						return Promise.reject(err);
                          });
}

export function readMessages(id)
{
  return fetch('https://api.wteam.chat/messaging-service/messages/'+id+'/read', {
      headers: { 'x-auth-token': token, 'Access-Control-Allow-Origin': '*', 'Content-Type': 'application/json' },
      method: 'POST',
      body: undefined
    })
}


