import React, { useState, useEffect } from 'react';
import logo from './logo.svg';
import icons from "./assets/image/icons.svg";
import './App.css';
import * as api from './api';
import UrlParse from 'url-parse';
import classnames from 'classnames';
const electron =  window.require("electron").remote;
var shell = window.require('electron').shell;

function App() {

  const [text, setText] = useState("");
  const [messages, setMessages] = useState([]);
  const [busy, setBusy] = useState(false);
  const [user, setUser] = useState({});
  const [roomId, setRoomId] = useState("");
  const [userInfos, setUserInfos] = useState([]);
  let allMessages = [], activeUsers = [], userInfoss = [], token, fromId, toId, otherUserInfo, rid;

  useEffect(() => {
    

    async function fetchData() {
      const urlParser = new UrlParse(window.location.href, true);
      token = urlParser.query.token;
      fromId = urlParser.query.fromId;
      toId = urlParser.query.toId;
      rid = urlParser.query.roomId;
      setRoomId(urlParser.query.roomId);
      api.setToken(token);
      let userInfo = await api.getAuthUser();
      api.setUserInfo(userInfo);
      setUser(userInfo);
      userInfoss.push(userInfo);
      setUserInfos([...userInfoss]);
      if(toId){
        otherUserInfo = userInfo.id == fromId ? await api.getUser(toId) : await api.getUser(fromId);
        api.setOtherUserInfo(otherUserInfo);
        userInfoss.push(otherUserInfo);
        setUserInfos([...userInfoss]);
      }
      
      
      let userType = userInfo.id == fromId ? 1 : 2;
      api.setUserType(userType);

      if(!rid)
        api.readMessages(otherUserInfo.id);

      connect(token);

      if(rid){
        api.getRoomMessages(rid).then(dmessages => {
          if(dmessages && dmessages.length){

            dmessages.forEach( function(element, index) {
              if(element && element.message){
                element.message = linkify(element.message);
              }
            });

            allMessages.push(...dmessages);
            

            setMessages([...dmessages]);

            let userIds = dmessages.map(m => m.fromId), usersCount = 0;
            userIds = userIds.filter((id, i) => userIds.indexOf(id) == i && id != userInfo.id);

            userIds.forEach(async(id) => {
              let ui = await api.getUser(id);
              userInfoss.push(ui);
              setUserInfos([...userInfoss]);
              usersCount++;
              if(usersCount == userIds.length){
                userInfoss.forEach(async (info, ind) => {
                  if(info.avatar){
                    let url = await api.getFileUrl(info.avatar);
                    userInfoss[ind].avatarUrl = url;
                    setUserInfos([...userInfoss]);
                  }
                });

              }
            });
            
            setTimeout(() => {
              var objDiv = document.querySelector(".teammates-chat__messages");
              objDiv.scrollTop = objDiv.scrollHeight;
            });
          
          }
        });
      } else {
        api.getMessages(otherUserInfo.id).then(dmessages => {
          if(dmessages && dmessages.length){
            dmessages.forEach( function(element, index) {
              if(element && element.message){
                element.message = linkify(element.message);
              }
            });

            allMessages.push(...dmessages);

            

            setMessages([...dmessages]);

            userInfoss.forEach(async (info, ind) => {
                  if(info.avatar){
                    let url = await api.getFileUrl(info.avatar);
                    userInfoss[ind].avatarUrl = url;
                    setUserInfos([...userInfoss]);
                  }
                });

            setTimeout(() => {
              var objDiv = document.querySelector(".teammates-chat__messages");
              objDiv.scrollTop = objDiv.scrollHeight;
            });
          }
        });
      }
      
    };

    fetchData();  

    document.addEventListener('click', function (event) {
      if (event.target.tagName === 'A' && event.target.href.startsWith('http')) {
        event.preventDefault()
        shell.openExternal(event.target.href)
      }
    })
    
  }, []);

  function connect(token){
    let es = new EventSource(`https://api.wteam.chat/messaging-service/notification/feed?token=` + token);
        console.log("event source: ", es);
        es.onerror = (err) => {
          console.log("error: ", err);
            setTimeout(() => {
                es.close();
                connect();
            }, 400);
        };
        es.onmessage = async (event) => {
            console.log("add message event: ", event);
            let data = JSON.parse(event.data);

            if(data.type == 2){
              if(roomId && data.data.roomId == roomId){
                let message = data.data;
                message.message = linkify(message.message);
                allMessages.push(message);

                setMessages([...allMessages]);

                if(userInfos.findIndex(i => i.id == message.fromId) == -1){
                    let ui = await api.getUser(message.fromId);
                    userInfoss.push(ui);
                    setUserInfos([...userInfoss]);

                    if(ui.avatar){
                      let url = await api.getFileUrl(ui.avatar);
                      userInfoss[userInfoss.length - 1].avatarUrl = url;
                      setUserInfos([...userInfoss]);
                    }
                  }

                setTimeout(() => {
                  var objDiv = document.querySelector(".teammates-chat__messages");
                  objDiv.scrollTop = objDiv.scrollHeight;
                });
              } else if(!roomId){
                let message = data.data;
                message.message = linkify(message.message);
                allMessages.push(message);

                setMessages([...allMessages]);

                setTimeout(() => {
                  var objDiv = document.querySelector(".teammates-chat__messages");
                  objDiv.scrollTop = objDiv.scrollHeight;
                });
              }
              
            } else if(data.type == 1){
              activeUsers = [];
              activeUsers.push(...data.data.users);
              let otherUserInfo = api.getOtherUserInfo();
              let user = activeUsers.find(u => u && otherUserInfo && u.id == otherUserInfo.id);
              if(!user || user.inmeeting){
                setBusy(true);
              } else {
                setBusy(false);
              }
            }
        };
  }

  function closeChat(){
    window.close();
  }

  function getFile(id){
    //console.log('file url: ', id, userInfos.find(ind => ind.id == id), userInfos.find(ind => ind.id == id).avatarUrl);
    return userInfos.find(ind => ind.id == id) && userInfos.find(ind => ind.id == id).avatarUrl ? userInfos.find(ind => ind.id == id).avatarUrl : null;
  }

  function getMessageTime(date){
    return (new Date(date)).toLocaleString();
  }

  function getMessageAuthor(id){
    return userInfos.find(ind => ind.id == id) && userInfos.find(ind => ind.id == id).name ? (userInfos.find(ind => ind.id == id).name.first) : '';
  }

  function startVideoCall(id) {
    console.log("user id: ", id);
    let user = api.getUserInfo();
    user.uType = user.type;
    let otherUserInfo = api.getOtherUserInfo();
    let noti = {
      code: "user.video-call",
      actingUser: user,
      otherUser: otherUserInfo,
    };

    

    api
      .addNotification(noti)
      .then((data) => {
        let fromId = user.id;

        let path = electron.getGlobal('sharedPath'); 
        let filePath = electron.getGlobal('filePath'); 

        const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
          width: 300,
          height: 500,
          title: "Wteam call",
          frame: false,
          transparent: true,
          resizable: false,
          webPreferences : { 
            preload: filePath + '../public/preload.js',
            nodeIntegration: true,
            enableRemoteModule: true,
            webSecurity: false
          }
        });
        callWindow.loadURL(path + '../call/call.html?roomId=' + otherUserInfo.id +
            "&fromId=" +
            fromId +
            "&token=" +
            api.getToken());
      })
      .catch((err) => {
        console.log("members error: ", err);
      });
  }

  function startAudioCall(id) {
    let user = api.getUserInfo();
    user.uType = user.type;
    let otherUserInfo = api.getOtherUserInfo();
    let noti = {
      code: "user.video-call",
      actingUser: user,
      otherUser: otherUserInfo,
    };

    

    api
      .addNotification(noti)
      .then((data) => {
        let fromId = user.id;

        let path = electron.getGlobal('sharedPath'); 
        let filePath = electron.getGlobal('filePath'); 

        const BrowserWindow = electron.BrowserWindow;
          let callWindow = new BrowserWindow({
          width: 300,
          height: 500,
          title: "Wteam call",
          frame: false,
          transparent: true,
          resizable: false,
          webPreferences : { 
            preload: filePath + '../public/preload.js',
            nodeIntegration: true,
            enableRemoteModule: true,
            webSecurity: false
          }
        });
        callWindow.loadURL(path + '../call/call.html?roomId=' + otherUserInfo.id +
            "&fromId=" +
            fromId +
            "&token=" +
            api.getToken());
      })
      .catch((err) => {
        console.log("members error: ", err);
      });
  }

  function handleKeyPress (event){
    console.log("room id: ", roomId, userInfoss);
  if(event.key === 'Enter' && text && text.trim().length){
    if(roomId){
      setTimeout(() => {
      let user = api.getUserInfo();
      let ids = userInfos.map(i => i.id).filter(i => i.id != user.id);
      let message = {
        fromId: user.id,
        message: text,
        roomId: roomId,
        involved: ids
      };

      api.sendMessage(message);

      setText("");
    });
    } else {
      setTimeout(() => {
      let user = api.getUserInfo(), otherUser = api.getOtherUserInfo();

      let message = {
        fromId: user.id,
        message: text,
        involved: [user.id, otherUser.id]
      };

      api.sendMessage(message);

      setText("");
    });
    }
    
    
  }
}
  
  function hideAfter(id){
    let index = messages.findIndex(m => m.id == id);

    if(index > 0 && messages[index - 1].fromId == messages[index].fromId)
      return true;
    else
      return false;
  }

  function hideBefore(id){
    let index = messages.findIndex(m => m.id == id);

    if(index < (messages.length - 1) && messages[index + 1].fromId == messages[index].fromId)
      return true;
    else
      return false;
  }

  function linkify(inputText) {
    var replacedText, replacePattern1, replacePattern2, replacePattern3;

    //URLs starting with http://, https://, or ftp://
    replacePattern1 = /(\b(https?|ftp):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/gim;
    replacedText = inputText.replace(replacePattern1, '<a href="$1" target="_blank">$1</a>');

    //URLs starting with "www." (without // before it, or it'd re-link the ones done above).
    replacePattern2 = /(^|[^\/])(www\.[\S]+(\b|$))/gim;
    replacedText = replacedText.replace(replacePattern2, '$1<a href="http://$2" target="_blank">$2</a>');

    //Change email addresses to mailto:: links.
    replacePattern3 = /(([a-zA-Z0-9\-\_\.])+@[a-zA-Z\_]+?(\.[a-zA-Z]{2,6})+)/gim;
    replacedText = replacedText.replace(replacePattern3, '<a href="mailto:$1">$1</a>');

    return replacedText;
}

  return (
    <div className="App">
      <div
            className="teammates-chat open"
          >
            <div className="teammates-chat__body">
              <div className="teammates-chat__content">
                <div className="teammates-chat__head">
                {!busy && (
                  <>
                  <button className="teammates-chat__type" onClick={() => {
                      startVideoCall();
                    }}>
                    <svg className="icon icon-video">
                      <use href={icons + "#icon-video"}></use>
                    </svg>
                  </button>
                  <button className="teammates-chat__type" onClick={() => {
                      startAudioCall();
                    }}>
                    <svg className="icon icon-talk">
                      <use href={icons + "#icon-talk"}></use>
                    </svg>
                  </button>
                  </>
                  )}
                  <button
                    className="teammates-chat__type close"
                    onClick={() => {
                      closeChat();
                    }}
                  >
                    <svg className="icon icon-close">
                      <use href={icons + "#icon-close"}></use>
                    </svg>
                  </button>
                </div>

                {/*<div className="teammates-chat__messages">
                {messages.map((message, i) => (
                  <div key={message.id}
                      className={classnames('teammates-chat__text', {
                        'right' : message.fromId == user.id,
                        'left' : message.fromId != user.id
                      })}
                  >
                    <span>{message.message}</span>
                  </div>
                ))}
                </div>*/}

                <div
                    className="teammates-chat__messages"
                  >
                  {messages.map((message, i) => (
                    <>
                    {message.fromId != user.id && (
                    <div key={i} 
                           className="message-own"
                    >
                    {!hideAfter(message.id) && 
                      <div className="message-own__image">
                      {getFile(message.fromId) && (
                        <img src={getFile(message.fromId)} alt="person" />
                        )}
                      {!getFile(message.fromId) && (
                        <div>
                          {getMessageAuthor(message.fromId).charAt(0)}
                        </div>
                        )}
                        <div className="message-own__image-active">
                          <svg className="icon icon-circul online">
                            <use href={icons + "#icon-circul"}></use>
                          </svg>
                        </div>
                      </div>
                    }
                      <div className="message-own__content">
                      {!hideAfter(message.id) && 
                        <div className="message-name">
                          <span>{getMessageAuthor(message.fromId)}</span>
                        </div>
                       }
                        <div className={classnames('message-sentence', {
                          'message-sentence--hidden' :hideAfter(message.id)
                        })}>
                          <span className="message-sentence__own" dangerouslySetInnerHTML={{ __html: message.message }}>
                            
                          </span>
                          <small className="message-sentence__time">
                            {getMessageTime(message.createdAt)}
                          </small>
                          
                        </div>
                      </div>
                    </div>
                  )}

                  {message.fromId == user.id && (
                    <div key={i} 
                           className="message-own you"
                    >
                      
                      <div className="message-own__content">
                      {!hideAfter(message.id) && 
                        <div className="message-name">
                          <span>{getMessageAuthor(message.fromId)}</span>
                        </div>
                      }
                        <div className={classnames('message-sentence', {
                          'message-sentence--hidden' :hideAfter(message.id)
                        })}>
                          <span className="message-sentence__own" dangerouslySetInnerHTML={{ __html: message.message }}>
                          </span>
                          <small className="message-sentence__time">
                            {getMessageTime(message.createdAt)}
                          </small>
                          
                        </div>
                      </div>
                      {!hideAfter(message.id) && 
                      <div className="message-own__image">
                        {getFile(message.fromId) && (
                        <img src={getFile(message.fromId)} alt="person" />
                        )}
                      {!getFile(message.fromId) && (
                        <div>
                          {getMessageAuthor(message.fromId).charAt(0)}
                        </div>
                        )}
                        <div className="message-own__image-active">
                          <svg className="icon icon-circul online">
                            <use href={icons + "#icon-circul"}></use>
                          </svg>
                        </div>
                      </div>
                      }
                    </div>
                  )}

                  </>
                  ))}
                  </div>

                <div className="teammates-tools__write teammates-chat__write">
                  <input value={text} onChange={(e) => setText(e.target.value)}
                    type="text" onKeyPress={(event) => {handleKeyPress(event)}}
                    placeholder="Type something which make me happy"
                  />
                </div>
              </div>
            </div>
          </div>
    </div>
  );
}

export default App;
